.editor-styles-wrapper .wp-block-nortic-plugin-resources-section {
  padding: 4rem 0;
  background-color: var(--wp--preset--color--nortic-white);
}

.editor-styles-wrapper
  .wp-block-nortic-plugin-resources-section
  .services-section-header
  .services-section-heading::after {
  content: "";
  display: block;
  width: 73px;
  padding-top: 8px;
  border-bottom: 4px solid var(--wp--preset--color--nortic-red);
}

.editor-styles-wrapper
  .wp-block-nortic-plugin-resources-section
  > .block-editor-inner-blocks
  > .block-editor-block-list__layout,
.wp-block-nortic-plugin-resources-section .resources-section-content-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}

/* frontend */

.wp-block-nortic-plugin-resources-section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
  overflow: hidden;
}

.wp-block-nortic-plugin-resources-section .resources-section-content-wrapper {
  /* background: rgb(0,56,118); */
  /* background: linear-gradient(90deg, rgba(0,56,118,1) 0%, rgba(51,96,145,1) 3%, rgba(214,223,233,0) 6%, rgba(255,255,255,0) 50%, rgba(78,117,160,0) 94%, rgba(51,96,145,1) 97%, rgba(0,56,118,1) 100%); */
}

.resources-section-heading {
  color: var(--wp--preset--color--nortic-blue);
}

.resources-section-title {
  color: var(--wp--preset--color--nortic-dark);
}

.glide__arrow--left {
  display: none !important;
}

.glide__arrow--right {
  display: none !important;
}
